@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container-loader {
    width: 100vw;
    height: 100vh;
    top: 0%;
    position: absolute;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.8);
    box-sizing: border-box;
  }
  
  .lista_busquedas {
    margin-top: 0;
    height: 300px;
    overflow-y: scroll;
    width: 65%;
    margin: auto;
  }
  
  .loader {

    border: 16px solid var(--primary-color);
    border-radius: 50%;
    width: 90px;
    height: 90px;
    background: url(/static/media/pokeball.1faeca5f.png);
    background-size: cover;
    animation: spin 1s linear infinite;
    padding: 10px;
    margin: 0px auto;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
      border: 16px solid var(--secundary-color);
      background-color: var(--secundary-color);
    }
  
    25% {
      border: 8px solid var(--primary-color);
      background-color: var(--secundary-color);
    }
  
    50% {
      border: 0px solid var(--primary-color);
      background-color: var(--secundary-color);
  
    }
  
    75% {
      border: 8px solid var(--secundary-color);
      background-color: var(--primary-color);
    }
  
    100% {
      transform: rotate(360deg);
      border: 16px solid var(--secundary-color);
      background-color: var(--primary-color);
    }
  }
:root {
  --primary-color:rgb(238, 21, 21)  ;
  --secundary-color:rgb(240,240,240);
  --main-width: 65%
}
* {
  font-family: 'Press Start 2P', cursive;
}

body {
  background: rgb(238, 21, 21);
  background: var(--primary-color);
  border-radius: 50%;
}

.title {
  font-weight: normal;
  font-size: 3.5vw;
}
p {
  font-weight: bold;
  font-size: 30px;
}
li {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.2s;
}

li:hover {
  background-color: rgb(238, 21, 21);
  background-color: var(--primary-color);
  color: rgb(240,240,240);
  color: var(--secundary-color);
}
li p {
  font-size: 2.4vh;
}
img {
  width: 20%;
}

div.container {
  width: 100%;
  padding: 50px;
  margin: auto;
  box-sizing: border-box;
  text-align: center;
  background-color: rgb(240,240,240);
  background-color: var(--secundary-color);
  color: black;
  border-bottom: 5px solid black;
  
}

input {
  outline: none;
  padding: 10px 0px;
  background-color: rgb(240,240,240);
  background-color: var(--secundary-color);/*  */
  border: 2px solid rgb(238, 21, 21);
  border: 2px solid var(--primary-color);
  width: 65%;
  width: var(--main-width);
  font-size: 19px;
  padding-left: 10px;
  margin: 15px auto 0px auto;
}

input:hover {
  box-shadow: 0px 0px 1px 1px rgba(238, 21, 21, 0.8);
}
.lista-busquedas{
  width: 65%;
  width: var(--main-width);
  height: 300px;
  margin: auto;
  overflow-y: scroll;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;

}

span img {
  width: 50px;
  height: 50px;
}

.pokedex img {
  width: 20vw;
  height: 20vw;
}


@media (max-width:800px) {
  div.container {
    width: 100%;
    border-bottom: 3px solid black;
    padding: 40px 0;
  }
 .title{
   font-size: 7vw;
 }

  img {
      width: 60%;
    }
  .container-loader p {
    font-size: 20px;
    text-align: center;
  }

  .lista-busquedas {
    height: 250px;
    width: 100%;
    outline: 2px solid rgb(238, 21, 21);
    outline: 2px solid var(--primary-color);
  }

  p {
    font-size: 5vw;
  }

  input {
    width: 100%;
    font-size: 13.5px;
    padding: 10px 0px;
    padding-left: 10px;
  }



}
